// Nome do arquivo: lib/supabaseClient.ts
import { createClient } from '@supabase/supabase-js';

// As variveis de ambiente so lidas do .env.local
const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL;
const supabaseKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY;

// Verifica se as variveis esto definidas
if (!supabaseUrl || !supabaseKey) {
  throw new Error('As variveis de ambiente NEXT_PUBLIC_SUPABASE_URL e NEXT_PUBLIC_SUPABASE_ANON_KEY so obrigatrias.');
}

// Cria e exporta o cliente Supabase
export const supabase = createClient(supabaseUrl, supabaseKey);